import React from 'react'; // React 
import './TodoItem.css'; //   

function TodoItem({ todo, onToggle, onDelete }) { //    (todo),  ,   props 
  return (
    <li className={`todo-item${todo.completed ? ' completed' : ''}`}> {/*    li,   completed   */}
      <span
        className="todo-text" //     
        onClick={() => onToggle(todo.id)} //     
        tabIndex={0} //  ( ) 
        role="button" // :  
        aria-pressed={todo.completed} // :   
      >
        {todo.text} {/*     */}
      </span>
      <button className="delete-btn" onClick={() => onDelete(todo.id)} aria-label=""></button> {/*   */}
    </li>
  );
}

export default TodoItem; // TodoItem  
