import React, { useState } from 'react';
import './TodoForm.css';

function TodoForm({ onAdd }) { //   onAdd  props 
  const [value, setValue] = useState(''); //     value    setValue  ,   

  //     
  const handleSubmit = (e) => {
    e.preventDefault(); //   () 
    if (!value.trim()) return; //     
    onAdd(value.trim()); //      
    setValue(''); //  
  };

  return (
    <form className="todo-form" onSubmit={handleSubmit}> {/*  ,   handleSubmit  */}
      <input
        className="todo-input" //   
        type="text" //   
        placeholder="  ..." //    
        value={value} //   value  
        onChange={(e) => setValue(e.target.value)} //    value  
      />
      <button className="todo-btn" type="submit"></button> {/*     */}
    </form>
  );
}

export default TodoForm; // TodoForm  
