import React from 'react'; // React 
import TodoItem from './TodoItem'; //     
import './TodoList.css'; //   

function TodoList({ todos, onToggle, onDelete }) { //    (todos),  ,   props 
  return (
    <ul className="todo-list"> {/*     ul */}
      {todos.length === 0 ? (
        <li className="empty">  .</li> //     
      ) : (
        todos.map((todo) => (
          // todos    todo TodoItem  
          <TodoItem
            key={todo.id} //    key (React    )
            todo={todo} //    
            onToggle={onToggle} //    
            onDelete={onDelete} //   
          />
        ))
      )}
    </ul>
  );
}

export default TodoList; // TodoList  
